export default (isEdit) => {
  let eventFormContent = (isEdit) ?
    `<a href="/" class="button button--gray" data-navigo></a>
      <button class="button button--gray"></button>`
    : `<a href="/" class="button button--gray" data-navigo></a>
    <button class="button button--blue button--disabled"> </button>`;

  return  eventFormContent;
}



